.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.error {
  height: 30px;
  width: 90%;
  padding: 5px;
  font-size: 18px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 20px;
  border-radius: 5px;
  border: 3px solid red;
  color: red;
  background: grey;
}

.success {
  height: 30px;
  width: 90%;
  padding: 5px;
  font-size: 18px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 20px;
  border-radius: 5px;
  border: 3px solid green;
  color: green;
  background: lightgray;
}

body{
  margin-left: 5px;
  font-size: 15px;
  padding-left: 10px;
}

.inputBlock {
  width: 400px;
  display: flex;
  padding-bottom: 5px;
}
.label {
  flex: 1;
  font-weight: bold;
}
.control {
  flex: 4;
}

.btn {
  width: 100px;
  height: 30px;
  background-color: #61dafb;
  font-weight: bold;
  margin-left: 30px;
}

.add {
  background-color: steelblue;  
  border: 2px solid green;
}

.delete {
  background-color: red;
  border: 2px solid lightcoral;
}